body{
  background-color: #661d1d
}

.headerColor{
  background-color: #ba7e0f
}

.textMainColor{
  background-color: #302f6e
}

.element{
  background-color: #661d1d;
  width: 100%;
  text-align: center;
  border: 5px solid #ba7e0f;
  border-radius: 16px;
  padding: 60px;
}

.element img {
  border: 1px solid #ddd;
  border-radius: 15px;
  padding: 20px;
  width: 100%;
  height: auto;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.fluid_container{
  margin: auto;
  padding: 30px;
}

.fluid_container_header{
  margin: auto;
  padding: 5px;
}

.button{
  margin: auto;
  padding: 15px;
}

